<template>
  <div>
    <h1 class="page-title">{{ $t('__exchange') }}</h1>
    <p class="choice-rates-title">選擇欲交換貨幣</p>
    <div class="choice-rates">
      <span
        v-for="(item, keys, idx) in ratesItem"
        :key="keys"
        @click="exchangeIn(item, keys, idx) "
        class="badge badge-secondary"
        :class="[ IN_rates === idx ? 'badge-primary' : '']"
      ><span v-html=" $t('__All_currency['+`'${keys}'`+'].currency') "></span></span>
    </div>
    <p class="choice-rates-title">選擇欲兌換貨幣</p>
    <div class="choice-rates"> 
      <span
        v-for="(item, keys, idx) in ratesItem"
        :key="idx"
        @click="exchangeTo(item, keys, idx)"
        class="badge badge-secondary"
        :class="[ TO_rates === idx ? 'badge-primary' : '']"
      ><span v-html=" $t('__All_currency['+`'${keys}'`+'].currency') "></span></span>
    </div>
    <div class="see-more choice-rates-seemore">
      <button 
        class="btn btn-primary card-btn" 
        @click.prevent='Convert_rates'
      >兌換匯率</button>
    </div>
  </div>
</template>

<script>
const Cryptr = require('cryptr');
const cryptr = new Cryptr('myTotalySecretKey');
  export default {
    name: "Exchange",
    props: ["rates"],
    data: () => ({
      IN_rates: null,
      TO_rates: null,
      Exchange_rates:[],
      aaaa:1
    }),
    computed: {
      ratesItem() {
        return this.rates;
      }
    },
    methods: {
      exchangeIn({ Exrate,UTC }, keys, idx) {
        this.IN_rates = idx;
        this.Exchange_rates[0] = {
          Rate :keys,
          Name :this.keysFormat(keys) ,
          Exrate,
          UTC
        }
      },
      exchangeTo({ Exrate,UTC }, keys, idx) {
        this.TO_rates = idx;
         this.Exchange_rates[1] = {
          Rate :keys,
          Name :this.keysFormat(keys) ,
          Exrate,
          UTC
        }
      },
      Convert_rates(){
        if(this.Exchange_rates[0] && this.Exchange_rates[1]){
          gtag('event', 'select_content', {
            "content_type": "two-rates",
            "promotions" :this.Exchange_rates[0].Name + ' TO ' +this.Exchange_rates[1].Name,
            "event_category" :this.Exchange_rates[0].Name + ' TO ' +this.Exchange_rates[1].Name,
             "items": [
              {
                "id": this.Exchange_rates[0].Name,
                "name": this.Exchange_rates[0].Name,
                "Exrate": this.Exchange_rates[0].Exrate,
                "UPDATE-Time":this.Exchange_rates[0].UTC
              },
              {
                "id": this.Exchange_rates[1].Name,
                "name": this.Exchange_rates[1].Name,
                "Exrate": this.Exchange_rates[1].Exrate,
                "UPDATE-Time":this.Exchange_rates[1].UTC
              },
            ]
          });
          // this.$route.query.rates = this.Exchange_rates;
          var qqq ={
                IN_Rate : this.Exchange_rates[0].Rate,
                IN_Exrate: this.Exchange_rates[0].Exrate,
                OUT_Rate : this.Exchange_rates[1].Rate,
                OUT_Exrate: this.Exchange_rates[1].Exrate,
                UTC: this.Exchange_rates[0].UTC
              }
          var encryptedStringA = JSON.stringify(qqq)
          const encryptedString = cryptr.encrypt(encryptedStringA);
       
          console.log(encryptedString)
          this.$router.push(
            { name:'Convert', 
              query:{encryptedString},    
              params:{
                item:this.Exchange_rates[0].Name+'TO'+this.Exchange_rates[1].Name,
                language: 'tw',
              }
            });
        }else{ 
          let lang = this.$route.params.language;
          if(lang == 'tw'){
           alert( this.$i18n.messages.tw.__exchange_alert_notice)
          }else{
           alert( this.$i18n.messages.en.__exchange_alert_notice)
          }
        }
      },
      keysFormat(val) {
        if (val.length <= 6) {
          val = val.slice(3, 6);
        }
        return val;
      }
    },
   
  };
</script>

<style lang="scss" >
  .badge {
    cursor: pointer;
    text-align: left;
    padding: 0.4em 0.4em;
  }
  .badge-primary {
    background-color: #0062cc !important;
  }
</style>